.learnMoreNav {
  background-color: #fffad4;
  z-index: 3;

}

.learnMoreNavSticky {
  position: fixed;
  top: 4em;
  width: 100%;
  padding-left: 120px;
}

.learnMoreNavList {
  height: 80px;
  width: 60%;
  margin: auto;
  padding: 0;

  display: flex;
  flex-basis: 70%;

  list-style: none;

  justify-content: space-around;
  align-items: center;
  align-content: center;
}

.learnMoreNavListElem {
  background-color: #a71814;

  color: #fffad4;
  text-decoration: none;
  text-align: center;

  border-radius: 20px;
  font-weight: 700;
  font-size: 12px;

  padding: 8px 12px;
  min-width: 7%;
}

.learnMoreNavListElem:hover {
  background-color: #fffad4;
  color: #a71814;
  border: 1px solid #a71814;
  cursor: pointer;
}

.activeLearnMoreNavElem {
  background-color: #fffad4;
  color: #a71814;
  border: 1px solid #a71814;
}

@media (max-width: 1200px) {
  .learnMoreNavSticky {
    top: 5em;
    padding-left: 150px;
  }
}

