@media (max-width: 1024px) {

  .learnMoreNav {
    padding: 0;
  }

  .learnMoreNavSticky {
    padding-left: 0;
  }

  .learnMoreNavSticky .learnMoreNavList {
    margin-top: 20px;
  }

  .learnMoreNavList {
    width: 100%;
    height: auto;
    display: inline-block;
    overflow-x: auto;
    white-space:nowrap;

  }

  .learnMoreNavListElem {
    width: 80px !important;
    min-width: 80px !important;
    display: inline-block;
    margin: 10px;
  }
}