.learnMoreMain {
  margin: 0px;
  padding: 0px;
  width: 100vw;
  background-color: #fffad4;
  color: #a71814;
}

.headerImg {
  width: 100vw;
  object-fit: cover;
  display: flex;
  margin-top: 3em;
}

.learnMoreInlineLink {
  color: #fffad4;
  text-decoration: underline;
  display: inline-block;
  padding-bottom: 2px;
}

.additionalPluses {
  color: #fffad4;
  background-color: #a71814;
}

.additionalPlusesContainer {
  padding: 100px;
}

.additionalPlusesHeader {
  font-size: 3vw;
  font-weight: 700;
  margin-bottom: 80px;
}

.additionalPlusesList {
  display: flex;
  justify-content: space-between;
}

.additionalPlusesItem {
  width: 28%;
  float: left;
  display: flex;
}

.additionalPlusesItem * {
  font-size: 1.5vw;
}

.additionalPlusesImg {
  width: 100vw;
}

@media (max-width: 1024px) {
  .additionalPlusesContainer {
    padding: 10vw 4vw;
  }

  .additionalPlusesHeader {
    font-size: 6vw;
  }

  .additionalPlusesList {
    display: block;
  }

  .additionalPlusesItem {
    float: none;
    width: 80%;
    padding-bottom: 20px;
  }

  .additionalPlusesItem * {
    font-size: 4vw;
  }

  .plusImg {
  }
}

.plusImg {
  height: 39px;
  width: 39px;
  margin-right: 20px;
  font-weight: 700;
}

.ecoFriendlyHeader {
  font-size: 3.5vw;
  font-weight: 700;
}

.ecoFriendlyLeftHeader {
  font-size: 1.5vw;
  font-weight: 700;
  padding: 60px 0 60px 0;
  width: 80%;
}

.ecoFriendlyListItem {
  display: flex;
  padding: 20px 0 20px 0;
}

.ecoFriendlyCheckImg {
  height: 39px;
  width: 39px;
  margin-right: 20px;
  font-weight: 700;

}

.ecoFriendlyCheckText {
  font-size: 1.5vw;
}

.ecoFriendlyImg {
  width: 100%;
  margin-bottom: 80px;

}

.ecoFriendly {
  width: 100vw;
  background-color: #FEEAA1;
}

.ecoFriendlyContainer {
  padding: 100px;
}

.ecoFriendlyDividedContaineer {
  display: flex;
  justify-content: space-between;
}

.ecoFriendlyRight {
  width: 45%;
}

.ecoFriendlyLeft {
  width: 45%;
}

.ecoFriendlyImgMobile {
  display: none;
}

@media (max-width: 1024px) {
  .ecoFriendlyContainer{
    padding: 10vw 4vw;
  }

  .ecoFriendlyHeader {
    font-size: 6vw;
  }

  .ecoFriendlyLeftHeader {
    font-size: 4vw;
    padding: 20px 0 20px 0;
    width: 90%;
  }
  .ecoFriendlyDividedContaineer {
    display: block;
  }
  .ecoFriendlyRight {
    width: 100%;
  }
  .ecoFriendlyLeft {
    width: 100%;
  }
  .ecoFriendlyCheckText {
    font-size: 4vw;
  }
  .ecoFriendlyListItem {
    padding: 10px 0 10px 0;
  }
  .ecoFriendlyImg {
    display: none;
  }
  .ecoFriendlyImgMobile {
    display: block;
    width: 110%;
    transform: translateX(-5%);
  }
}

.faqHr {
  background-color: rgba($color: #a71814, $alpha: 1);
  border: none;
  height: 2px;
  box-shadow: none;
  margin: 0;
}

.faqLearnMore {
  background-color: #fffad4;

}

.faqContainer {
  padding: 100px;
}

.faqHeader {
  font-size: 3.5vw;
  font-weight: 700;
  margin-bottom: 20px;
}

.faqListContainer {
  display: flex;
  justify-content: space-between;
}

.faqList {
  width: 40%;
}

.faqItem {
  padding: 20px 0 20px 0;
}

.faqHelmet {
  font-weight: 700;
  font-size: 1.5vw;

  display: flex;
  align-items: center;
  justify-content: space-between;

}

.faqArrow {
  width: 18px;
  height: 12px;
}

.faqArrow:hover {
  cursor: pointer;
}

.faqText {
  font-size: 1.2vw;

}

@media (max-width: 1024px) {
  .faqContainer {
    padding: 10vw 4vw;
  }

  .faqHeader {
    font-size: 6.3vw;
    margin-bottom: 0;
  }

  .faqListContainer {
    display: block;
  }

  .faqList {
    width: 100%;
  }

  .faqItem {
    padding: 10px 0 10px 0;
  }

  .faqHelmet {
    font-size: 4.5vw;
  }

  .faqText {
    font-size: 4vw;
  }

  .faqArrow {

  }
}