.side {
    width: 100%;
    padding: 0;
    margin: 0;
    background-color: #FFFAD4;
    color: #a71814;

    display: flex;
    align-items: center;
    justify-content: center
}

.sideImgContainer {
    width: 50%;
    display: flex;
    overflow: hidden;
}

.sideImg {
    min-width: 100%;
    min-height: 105%;
    object-fit: cover;
}

.sideContent {
    width: 50%;
}

.sideContentContainer {
    width: 61%;
    margin: auto;
}

.sideCaption {
    font-size: 3.5vw;
    font-weight: 700;
    margin-bottom: 30px;
}

.sideText {
    font-size: 1.4vw;
}

@media (max-width: 1024px) {
    .side {
        display: block;
    }

    .sideImgContainer {
        width: 100%;
    }

    .sideImg {
        width: 100%;
    }

    .sideContent {
        width: 100%;
        display: flex;
    }

    .sideContentContainer {
        width: 100vw;
        padding: 10vw 4vw;
    }

    .sideCaption {
        font-size: 6.5vw;
    }

    .sideText {
        font-size: 4.2vw;
    }
}